@import "styles/variables";
@import "styles/mixins";

.wrapper {
  position: relative;
  background-color: $colorDark;
  background-image: url("../../../assets/images/GetConsultBanner__img.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  overflow: hidden;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $colorDark;
    opacity: 0.7;
    z-index: 1;
  }
}

.container {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 52rem;
  z-index: 1;
}

.slogan {
  @include h2;

  margin: 0;
}

.button {
  margin-top: 3.5rem;
}
