@import "styles/variables";
@import "styles/mixins";

.wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 40.5rem;
}

.contacts {
  position: absolute;
  top: -11.1rem;
  left: calc(50% - 7.6rem);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 4rem 3.8rem 6.4rem 3rem;
  width: 20.3rem;
  height: 20rem;
  background-color: $colorDark;
  z-index: 1;
}

.name {
  @include fontValue($colorLightest, 400);

  margin: 0;
}

.rank {
  @include fontLabel;

  margin: 0;
  opacity: 0.5;
}

.email {
  @include fontLabel;

  margin-top: 1.6rem;
  opacity: 0.5;
  transition-duration: $transitionDuration;

  &:hover {
    color: $colorHighlight;
    opacity: 1;
  }
}

.left {
  position: relative;
  background-color: $colorBgButton;
}

.photo {
  position: absolute;
  bottom: 0;
  right: 15rem;
  width: 30.5rem;
  height: 50rem;
  object-fit: cover;
}

.right {
  padding: 11.7rem calc(180 / 720 * 100%) 6.8rem 3.8rem;
  background-color: $colorBgPanel;
}

.caption,
.list {
  width: 51.4rem;
}

.caption {
  @include fontValue($colorLightest, 400);

  margin: 0;
}

.list {
  @include resetList;

  padding-right: 10%;
}

.item {
  @include fontValue($colorLightest, 300);

  &:not(:first-child) {
    margin-top: 2rem;
  }
}
