@import "styles/variables";
@import "styles/mixins";

.wrapper {
  position: relative;

  :global {
    .swiper-wrapper {
      @include resetList;
    }

    .swiper-container {
      padding-top: 16rem;
    }
  }
}

.button {
  position: absolute;
  top: 4.9rem;
  background-color: $colorBgButton;
  transition-duration: $transitionDuration;
  z-index: 1;

  &.buttonPrev {
    left: calc(50% + 12.8rem);
    transform: rotate(90deg);
  }

  &.buttonNext {
    left: calc(50% + 12.8rem + 11rem);
    border-top: 1px solid $colorBorderMain;
    transform: rotate(-90deg);
  }
}
.buttonDisabled {
  opacity: 0.3;

  svg {
    fill: $colorDarkest;
  }
}
