@import "styles/variables";
@import "styles/mixins";

.content {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 58.8rem;
  grid-column-gap: 5.3rem;
  padding-top: 2.1rem;

  &::before {
    content: "";
    position: absolute;
    bottom: 10.4rem;
    left: 10.7rem;
    width: 29.1rem;
    height: 34.1rem;
    background-image: url("../../../assets/images/PresentationBanner__img.png");
    background-size: cover;
  }
}

.slogan {
  @include h2($colorDark);

  margin: 0;
  text-align: right;
}

.descriptor {
  @include fontValue($colorDarkest, 500);

  grid-column: 2 / 3;
  grid-row: 2 / 3;
  margin: 0;
  padding-right: 18%;
}

.partners {
  @include h4;

  grid-column: 2 / 3;
  grid-row: 3 / 4;
  margin: 0;
  padding-top: 2rem;
}

.linkDownload {
  @include fontRegular($colorDark, 500);

  grid-column: 2 / 3;
  grid-row: 4 / 5;
  display: flex;
  align-items: center;
  margin-top: 15rem;
  background-color: $colorBgButton;
}

.buttonDisabled {
  margin-right: 5rem;
}
