@import "styles/variables";
@import "styles/mixins";

.wrapper {
  background-color: $colorLightest;
  border-bottom: 0.1rem solid $colorBorderMain;
}

.content {
  display: grid;
  grid-template-columns: 55.5rem auto;
  padding-bottom: 10rem;
}

.slogan {
  @include h2($colorDark);

  grid-column: 2 / 3;
  grid-row: 1 / 2;
  margin: 0;
  padding-left: 25%;
  text-align: right;
}

.recipe {
  @include fontRegular($colorDarkest, 400);

  grid-row: 1 / 2;
  margin: auto 0 0 0;
}

.descriptor {
  @include fontValue($colorDarkest, 500);

  grid-column: 1 / 2;
  margin: 2rem 0 0 0;
  padding-right: 10%;
}

.goal {
  @include h4;

  grid-column: 1 / 2;
  margin: 3rem 0 0 0;
}

.diagram {
  grid-column: 2 / 3;
  grid-row: 2 / 5;
  width: 43rem;
}
